<template>
  <div class="theme container-fluid text-white  d-flex justify-content-center">
    <div class="w-75 d-flex flex-column align-items-start">
      <div class="h3 my-3"></div>
      <div class="row songs">
        <div
          class="col-4 col-lg-3 mb-2 mb-sm-4 shadow"
          v-for="item in allSongs"
          :key="item.id"
          >
          <div class="card bg-dark text-white">
            <img :src="item.images[0].url" class="card-img" :alt="item.name">
            <div class="card-img-overlay">
              <h5 class="card-title sit-title">{{ item.name }}</h5>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      allSongs: []
    }
  },
  created () {
    const vm = this
    const head = { headers: { Authorization: 'Bearer m2CYdp5FHaDLHWhYoaHSIg==' } }
    // 
    this.axios.get('https://api.kkbox.com/v1.1/mood-stations?territory=TW', head)
      .then(function (res) {
        vm.allSongs = res.data.data
      })
      .catch(error => console.error(error))
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
.theme{
  background-color: #151727;
}
.songs{
  height: 310px;
  overflow-y: auto;
}
.songs::-webkit-scrollbar {
  width: 10px;
}
.songs::-webkit-scrollbar-thumb {
  background: #666;
  border-radius: 30px;
}
</style>
