<template>
  <div class="theme container-fluid text-white  d-flex flex-column align-items-center">
    <div class="card big-card text-white bg-transparent border-0">
      <div class="bigScreen d-flex justify-content-center align-items-center position-relative">
        <img :src="playlists[0].images[2].url" class="card-img" alt="...">
        <div class="mask"></div>
      </div>
      <div class="card-img-overlay big-title">
        <h5 class="card-title h2">{{ playlists[0].title }}</h5>
        <p class="card-text text-truncate">{{ playlists[0].description }}</p>
        <a
          class="btn-blue-solid"
          :href="playlists[0].url"
          title=" KKBOX "
          target="_blank"
        > KKBOX </a>
      </div>
    </div>
    <div class="position-relative w-75 heig">
      <div class="row otherSongs">
        <div
          class="col-4 shadow"
          v-for="item in otherPopularSongs"
          :key="item.id"
          >
          <a :href="item.url" target="_blank">
            <div class="card text-white bg-transparent">
              <div class="otherImg">
                <img :src="item.images[2].url" class="card-img" alt="...">
              </div>
              <div class="card-img-overlay otherTitle">
                <h5 class="card-title h3">{{ item.title }}</h5>
              </div>
            </div>
          </a>
        </div>
      </div>
    </div>
    <div class="row w-75 mt-sm-5">
      <div class="col-sm-6">
        <p class="h3 mb-4 text-left font-weight-bold"></p>
        <ul class="list-unstyled">
          <li
            class="media mb-3 pb-3 border-bottom border-secondary text-left"
            v-for="(item, index) in chineseSongs"
            :key="item.id"
            >
            <p class="my-auto mx-2 m-sm-4 text-white-50">{{ `0${index+1}` }}</p>
            <a :href="item.url" target="_blank">
              <img :src="item.album.images[0].url" class="mr-3" alt="..." style="width: 70px;">
            </a>
            <a :href="item.url" target="_blank" class="media-body my-auto bottomLine w-25 w-sm-100">
              <p class="m-0 text-white text-truncate" style="font-size: 15px;">{{ item.name }}</p>
              <p class="text-white-50 m-0 text-truncate" style="font-size: 10px;">{{ item.album.artist.name }}</p>
            </a>
            <a :href="item.album.url" class="mt-auto mr-1 text-white-50 album" target="_blank">
              <i class="fas fa-compact-disc"> </i>
            </a>
          </li>
        </ul>
      </div>
      <div class="col-sm-6">
        <p class="h3 mb-4 text-left font-weight-bold"></p>
        <ul class="list-unstyled">
          <li
            class="media mb-3 pb-3 border-bottom border-secondary text-left"
            v-for="(item, index) in westernSong"
            :key="item.id"
            >
            <p class="my-auto mx-2 m-sm-4 text-white-50">{{ `0${index+1}` }}</p>
            <a :href="item.url" target="_blank">
              <img :src="item.album.images[0].url" class="mr-3" alt="..." style="width: 70px;">
            </a>
            <a :href="item.url" target="_blank" class="media-body my-auto bottomLine w-25 w-sm-100">
              <p class="m-0 text-white text-truncate" style="font-size: 15px;">{{ item.name }}</p>
              <p class="text-white-50 m-0 text-truncate" style="font-size: 10px;">{{ item.album.artist.name }}</p>
            </a>
            <a :href="item.album.url" class="mt-auto mr-1 text-white-50 album" target="_blank">
              <i class="fas fa-compact-disc"> </i>
            </a>
          </li>
        </ul>
      </div>
    </div>
    <Situation />
  </div>
</template>

<script>
import Situation from '@/components/Situation.vue'

export default {
  components: {
    Situation
  },
  data () {
    return {
      playlists: [{
        url: '',
        title: '',
        description: '',
        images: [{}, {}, {
          url: ''
        }]
      }],
      chineseSongs: [],
      westernSong: []
    }
  },
  created () {
    const vm = this
    const head = { headers: { Authorization: 'Bearer m2CYdp5FHaDLHWhYoaHSIg==' } }
    // 
    this.axios.get('https://api.kkbox.com/v1.1/new-hits-playlists?territory=TW', head)
      .then(function (res) {
        vm.playlists = res.data.data
        // 
        vm.axios.get(`https://api.kkbox.com/v1.1/new-hits-playlists/${res.data.data[0].id}?territory=TW`, head)
          .then(function (res) {
            res.data.tracks.data.length = 5
            vm.chineseSongs = res.data.tracks.data
          })
          .catch(error => console.error(error))
        // 
        vm.axios.get(`https://api.kkbox.com/v1.1/new-hits-playlists/${res.data.data[1].id}?territory=TW`, head)
          .then(function (res) {
            res.data.tracks.data.length = 5
            vm.westernSong = res.data.tracks.data
          })
          .catch(error => console.error(error))
      })
      .catch(error => console.error(error))
  },
  computed: {
    otherPopularSongs () {
      var arr = [...this.playlists]
      arr.splice(0, 1)
      return arr
    }
  }
}
</script>

<style lang="scss">
.heig{
  height: 180px;
}
.big-title{
  top: auto;
  bottom: 140px;
  margin-right: auto;
  margin-left: auto;
  width: 90%;
  background-color: #0f112349;
  border-radius: 10px;
}
.bigScreen{
  height: 600px;
  overflow: hidden;
}
.btn-blue-solid {
    font-size: 13px;
    color: #fff;
    padding: 9px 17px;
    border-radius: 33px;
    background-color: #00aed8;
}
.btn-blue-solid:hover {
    background-color: #23c2e8;
    color: #fff;
    text-decoration: none;
    transition: 1s;
}
.otherSongs{
  position: absolute;
  bottom: 0px;
}
.otherImg{
  height: 300px;
  overflow: hidden;
  border-radius: 5px;
}
.otherTitle{
  top: auto;
}
.mask{
  position: absolute;
  width: 100%;
  height: 100%;
  background: -webkit-radial-gradient(ellipse,transparent 50%,#151727);
}
.album:hover{
  color: #fff !important;
  transition: 0.5s;
}
.bottomLine:hover{
  text-decoration:none;
}
@media (max-width: 1200px) {
  .heig{
    height: 100px;
  }
  .otherImg{
    height: 200px;
  }
  .otherTitle{
    h5{
      font-size: 20px;
    }
  }
}
@media (max-width: 768px) {
  .heig{
    display: none;
  }
  .big-title{
    bottom: 15px;
  }
}
@media (max-width: 576px) {
  .big-card{
    height: 300px;
  }
  .h2{
    font-size: 5px;
  }
  .sit-title{
    display: none;
  }
}
</style>
